import React from 'react';
import context from '../context'

//let theIns = 'tree/gh-pages/MusyngKite/' + context.instrument + '-mp3'

let bar = [
    [1, 3, 5],
    [3],
    [5],
    [3]
]
let pitch = 'C4'
let scale = ['C4', 'D4', 'E4', 'F4', 'G4', 'A4', 'B4']

class barplayer extends React.Component {
    state = {
        play: false,
        audio: [],
    }
    componentDidMount(){
        this.updateAudio(bar)
    }
    componentWillReceiveProps(prevProps){
        console.log('barplayer',prevProps)
        this.updateAudio(bar)
    }

    updateAudio(bar) {
        
        let audio = []
        for (var i in bar) {
            audio.push([])
            for (var j in bar[i]) {
                let url = 'tree/gh-pages/MusyngKite/' +context.instrument + '-mp3' + '/' + scale[bar[i][j] - 1] + '.mp3'
                //console.log(url)
                audio[i].push(
                    new Audio(url)
                )
            }
        }
        this.setState({
            audio
        })
    }



    /*componentDidMount() {
        this.updateAudio(bar)
    }*/

    /*componentWillUnmount() {
        audio.removeEventListener('ended', () => this.setState({ play: false }));
    }*/

    /*togglePlay = () => {
        this.setState({ play: !this.state.play }, () => {
            this.state.play ? this.audio.play() : this.audio.pause();
        });
    }*/
    playbeat(beat){
        console.log(beat)
        return new Promise(resolve=>{
            for(var i in beat){
                beat[i].play()
                setTimeout(function(){
                    beat[i].pause();
                    beat[i].currentTime = 0;
                    resolve(1)
                }, 600);
            }
        })       
    }
    async playbar(bar){
        for(var i in bar){
            await this.playbeat(bar[i])
        }
        this.setState({play:false})
    }

    togglePlay = () => {
        let audio = this.state.audio
        //console.log(audio)
        this.setState({ play: true });
        if(!this.state.play){
            this.playbar(audio)
        }
    }


render() {
    return (
        <div>
            <button onClick={this.togglePlay}>{this.state.play ? 'stop' : 'play'}</button>

        </div>
    );
}
}

export default barplayer;