import React, { Component } from 'react';


import Keyboard from './keyboard'
import Selector from './selector'
import Bplayer from '../player/barplayer'

import context from '../context'
let TheInstru = context.instrument
//import midi from './instruments/midi'


class oldApp extends Component {
  state = {
    theInstrument: 'accordion',
  }

  //console.log('width'+screen.width, 'height'+screen.height)
  componentWillMount(){
    context.instrument='accordion'
  }
  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener("resize", this.updateWindowDimensions.bind(this));
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateWindowDimensions.bind(this));
  }

  updateWindowDimensions() {
    //console.log('width:' + window.innerWidth, 'height:' + window.innerHeight)
    context.screenSize={width:window.innerWidth,height:window.innerHeight}
  }

  chooseInstru = (value) => {
    this.setState({
      theInstrument: value,
    })
    context.instrument = value
  }

  render() {
    return (
      <div>
        <TheInstru.Provider value={this.state.theInstrument}>
          <Selector value={this.state.theInstrument} chooseInstru={this.chooseInstru} />
        </TheInstru.Provider>

        <Keyboard />
        <Bplayer instrument={this.state.theInstrument}/>
        
      </div>
      
    )
  }
}

export default oldApp;
