import React from 'react';
//import axios from 'axios'
import { Button, ButtonGroup, ButtonToolbar } from 'reactstrap';

import Onekey from './onekey'
import context from '../context'
//import { render } from 'react-dom';

//import theIns from '../resources/sound/cello-mp3'

class keyboard extends React.Component {
  state = {
    //theInstrument: context.instrument
  }
  
  componentWillReceiveProps(){
    console.log('keyboard receive props')
    this.render()
  }

  row(nn) {
    
    let theIns= 'tree/gh-pages/MusyngKite/'+context.instrument+'-mp3'
    const keyname = ['Do', 'Re', 'Mi', 'Fa', 'So', 'La', 'Ti']
    const keyvalue = ['C', 'D', 'E', 'F', 'G', 'A', 'B']

    let output = []
    for (var i in keyname) {
      let value = keyvalue[i] + String(nn)
      let theKey= theIns+'/'+value+'.mp3'
      
      output.push(
        <Onekey key={value} theKey={theKey} keyname={value}></Onekey>
      )
    }
    return output
  }
  //console.log(row(3))
  render() {
    return (
      <div>
        <ButtonToolbar>
          <ButtonGroup>
            {this.row(3)}
          </ButtonGroup>
        </ButtonToolbar>
        <ButtonToolbar>
          <ButtonGroup>
            {this.row(4)}
          </ButtonGroup>
        </ButtonToolbar>
        <ButtonToolbar>
          <ButtonGroup>
            {this.row(5)}
          </ButtonGroup>
        </ButtonToolbar>
      </div>
    );
  }
}
export default keyboard;