import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import { Row, Col } from 'reactstrap';

import './App.css';
import Tools from './tools/page'
import Notation from './notation/page'

//import midi from './instruments/midi'


class App extends Component {

  render() {
    return (
      <Router>
      <div>
        <Row>
          <Col>
            <Link to="/">Home</Link>
          </Col>
          <Col>
            <Link to="/compose">Compose</Link>
          </Col>
          <Col>
            <Link to="/tools">Tools</Link>
          </Col>
        </Row>
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/compose">
          <Notation />
          </Route>
          <Route path="/tools">
          <Tools />
          </Route>
        </Switch>
      </div>
    </Router>
    )
  }
}
function Home() {
  return (
    <div>
      <h2>Home</h2>
    </div>
  );
}

export default App;
