
import instrumentNames from './instrumentNames'

import React, { useState } from 'react';
import { Card, CardTitle, Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';


const Example = (props) => {
    const [dropdownOpen, setDropdownOpen] = useState(false);

    const toggle = () => setDropdownOpen(prevState => !prevState);

    //const [selection, setSelection]=useState('select instrument')
    
    const instru = instrumentNames.map((value) => {
        
        return <DropdownItem key={value} onClick={() => props.chooseInstru(value)}>
            {value}
        </DropdownItem>
    })

    const insName = props.value.replace(/^\w/, c => c.toUpperCase())
    let wikistyle={
        padding:'0 0 10px 0'
    }
    let wikilink="https://en.wikipedia.org/wiki/"+props.value
    return (
        <Card body>
        <CardTitle>{insName}</CardTitle>
        <a href={wikilink} style={wikistyle}>wiki</a>
        <Dropdown isOpen={dropdownOpen} toggle={toggle}>
            <DropdownToggle caret>
                Select Instrument
        </DropdownToggle>
            <DropdownMenu>
                {instru}
            </DropdownMenu>
        </Dropdown>
      </Card>
        
    );
}

export default Example

