import React from 'react';

import { Button } from 'reactstrap';
//import theKey from '../resources/sound/accordion-mp3'


class onekey extends React.Component {
    state = {
        play: false,
        audio: new Audio(this.props.theKey)
    }

    updateAudio(theKey) {
        this.setState({ audio: new Audio(theKey) })

    }
    componentWillReceiveProps(prevProps) {
        //console.log(prevProps)
        this.updateAudio(prevProps.theKey)
    }

    /*componentDidMount() {
        console.log(this.props.theKey)
        this.updateAudio()
        //this.audio.addEventListener('ended', () => this.setState({ play: false }));

    }

    /*componentWillUnmount() {
        audio.removeEventListener('ended', () => this.setState({ play: false }));
    }*/

    /*togglePlay = () => {
        this.setState({ play: !this.state.play }, () => {
            this.state.play ? this.audio.play() : this.audio.pause();
        });
    }*/


    togglePlay = () => {

        let audio = this.state.audio
        //console.log(audio)
        this.setState({ play: !this.state.play });

        if (!this.state.play) {
            audio.play()
        }
        else {
            audio.pause()
            audio.currentTime = 0
            const currentNote = React.createContext({
                note: audio.src,
                dur: audio.currentTime
            })
            console.log(currentNote)
        }
    }

    render() {
        return (
            <div>
                <Button onClick={this.togglePlay}>{this.state.play ? 'stop' : 'play'}</Button>
            </div>
        );
    }
}

export default onekey;